













































var userLoginInfo, _self;
export default {
	data() {
		return {
			// array: ['男', '女'],
			// index: 0,
			personMessage: [],
			isuser: false,
			nickname: '',
			photo: '',
			disabled: true,
			iptoto:'http://static.lamamuying.com/static/images/face.png'
		};
	},
	onShow() {
		userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		if (!userLoginInfo.photo) {
			this.disabled = false;
		} else {
			this.disabled = true;
		}
		// 获取用户个人信息
		this.getPersonal();
		// console.log(uni.getStorageSync('userLoginInfo'));
	},
	methods: {
		// 		bindPickerChange: function(e) {
		// 		console.log('picker发送选择改变，携带值为：' + e.target.value)
		// 		this.index = e.target.value
		// 	},
		// 获取用户个人信息
		getPersonal() {
			uni.request({
				url: this.apiUrl + 'user/getUser',
				method: 'GET',
				data: {
					uid: userLoginInfo.id
				},
				success: res => {
					if (res.data.code === 0) {
						this.personMessage = res.data.data;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 修改用户昵称
		blu(event) {
			this.nickname = event.detail.value;
			if(!this.personMessage.photo){
				this.photo = this.iptoto;
			}else{
				this.photo = this.personMessage.photo;
			}
			//保存用户信息
			this.hold();
		},
		// 上传个人头像
		upload() {
			_self = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: res => {
					const tempFilePaths = res.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url: this.apiUrl + 'user/uploadHeader',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							'user': 'test'
						},
						success: uploadFileRes => {
							var ress = JSON.parse(uploadFileRes.data);
							if (ress.code == 0) {
								this.photo = ress.data;
								if (!this.personMessage.nickname) {
									this.nickname = '暂无用户名';
								} else {
									this.nickname = this.personMessage.nickname;
								}
								//保存用户信息
								this.hold();
							}
						}
					});
				},
				error: function(e) {
				},
				complete: () => {

				}
			});
		},
		// 保存用户信息
		hold() {
			uni.request({
				url: this.apiUrl + 'user/saveUser',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					id: userLoginInfo.id,
					nickname: this.nickname,
					photo: this.photo
				},
				success: res => {
					if (res.data.code === 0) {
						this.getPersonal();
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onLoad: function() {

	}
}
